.availability {
  text-align: center;
  margin-bottom: 20px;
  &__wrapper {
    padding: 91px 0 40px;
    background-color: $white;
    box-shadow: $lightShadow; }
  &__icon-wrapper {
    margin-bottom: -80px; }
  &__icon {
    display: inline-block;
    @include circle(100);
    @include vertical;
    .icon {
      @include icenter;
      font-size: 42px;
      color: $white; } }
  &__text {
    display: inline-block;
    max-width: 480px;
    margin-bottom: 20px; } }
